export function getSharedProps(props, state) {
    var disabled = props.disabled, error = props.error, positive = props.positive, adjoined = props.adjoined, size = props.size, required = props.required, resize = props.resize, readOnly = props.readOnly;
    var isFocused = state.isFocused;
    return {
        $isFocused: isFocused,
        $disabled: disabled,
        $error: error,
        $positive: positive,
        // @ts-ignore
        $adjoined: adjoined,
        // @ts-ignore
        $size: size,
        $required: required,
        $resize: resize,
        $isReadOnly: readOnly,
    };
}
